html{
  margin:0;
  padding:0;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
body{
flex-grow: 1;
}
.App {
  min-width: 100vw;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 1fr 22rem;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
}
.holder1{
  background: #282c34;
  grid-column: 1/2;
  grid-row: 1/3;
}
.holder2{
  background: #282c34;

  grid-column: 3/4;
  grid-row: 1/3;
}
.textHolder{
  grid-column: 2/3;
  grid-row: 1/2;
  display: flex;
  flex-direction: column;
  padding-bottom: 2rem;
}

.textSignUp{
display: flex;
grid-column: 2/3;
grid-row:2/3;
min-height: 14rem;
flex-direction: column;
justify-content: center;
align-items: center;
background: gray;
}
.signIn{
  display: none;
  grid-column: 2/3;
  grid-row:2/3;
  min-height: 14rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: gray;
  }
.message{
  display: none;
  grid-column: 2/3;
grid-row:2/3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: gray;
}
.sentMe{
 background: lightgreen;
 font-weight: 500;
 width: 42rem;
 min-height: 7rem;
 margin-top: 1rem;
 margin-left: 2rem;
 display: flex;
 flex-direction: column;
align-items: flex-end;
 box-shadow: 0px 0px 2px 2px black;
}
.otherMes{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  background: gray;
  font-weight: 500;
  width: 42rem;
  min-height: 7rem;
  margin-top: 1rem;
  margin-left: 44rem;
  box-shadow: 0px 0px 2px 2px black;

}
form{
  display: flex;
  flex-direction: column;
  min-width: fit-content;
  margin-top: 1rem;
}
input{
  border-radius: 3px;
  margin-bottom: 1rem;
}
button{
border-radius:3px ;
}
input:hover, input:focus{
  box-shadow: 0px 0px 2px 2px black;
}
button:hover{
  box-shadow: 0px 0px 2px 2px black;
}
label{
  margin-bottom: 1rem;
}
.badText{
font-weight: 600;
}
.na{
  width: fit-content;
  height: 1rem;
  margin-bottom: 0;
  margin-top: 0;
  margin-right: 1rem;
}
.textHo{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 41rem;
  height: 4rem;
  margin-bottom: 0;
  margin-top: 0;
}
.dateHo{
  width: fit-content;
  height: 1rem;
  margin-bottom: 0;
  margin-top: 0;
  margin-right: 1rem;
}
@media(max-width:600px){
  body{
    font-size: 12px;
  }
  .na{
    width: fit-content;
    height: 1rem;
    margin-bottom: 0;
    margin-top: 0;
    margin-right: 1rem;
  }
  .textHo{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 12rem;
    height: 4rem;
    margin-bottom: 0;
    margin-top: 0;
  }
  .dateHo{
    width: fit-content;
    height: 1rem;
    margin-bottom: 0;
    margin-top: 0;
    margin-right: 1rem;
  }
  .sentMe{
    background: lightgreen;
    font-weight: 500;
    width: 13rem;
    min-height: 7rem;
    margin-top: 1rem;
    margin-left: 2rem;
    display: flex;
    flex-direction: column;
   align-items: flex-end;
    box-shadow: 0px 0px 2px 2px black;
   }
   .otherMes{
    display: flex;
     flex-direction: column;
     align-items: flex-end;
     background: gray;
     font-weight: 500;
     width:13rem;
     min-height: 7rem;
     margin-top: 1rem;
     margin-left: 7rem;
     box-shadow: 0px 0px 2px 2px black;
   
   }
   form{
    width: 300px;
   }
}
@media(min-width:601px) and ( max-width:1000px){
  body{
