  }
  .na{
    width: fit-content;
    height: 1rem;
    margin-bottom: 0;
    margin-top: 0;
    margin-right: 1rem;
  }
  .textHo{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 19rem;
    height: 4rem;
    margin-bottom: 0;
    margin-top: 0;
  }
  .dateHo{
    width: fit-content;
    height: 1rem;
    margin-bottom: 0;
    margin-top: 0;
    margin-right: 1rem;
  }
  .sentMe{
    background: lightgreen;
    font-weight: 500;
    width: 20rem;
    min-height: 7rem;
    margin-top: 1rem;
    margin-left: 2rem;
    display: flex;
    flex-direction: column;
   align-items: flex-end;
    box-shadow: 0px 0px 2px 2px black;
   }
   .otherMes{
    display: flex;
     flex-direction: column;
     align-items: flex-end;
     background: gray;
     font-weight: 500;
     width:20rem;
     min-height: 7rem;
     margin-top: 1rem;
     margin-left: 12rem;
     box-shadow: 0px 0px 2px 2px black;
   
   }
   form{
    width:620px;
   }
}
